@import "style/mixins.module.scss";

.root {
    padding-top: 16px;

    border-top: 1px solid rgba(66, 78, 88, 0.1);
}

.list {
    padding: 0;
    margin: 0;
    list-style: none;
}

.link {
    position: relative;
    display: block;
    padding: 10px 40px;

    font-size: 17px;
    line-height: 24px;
    font-weight: 500;

    @include color("gray", "-4");
}

.icon {
    position: absolute;
    left: 0;
}

@media (min-width: 768px) {
    .list {
        display: flex;
        justify-content: space-between;
    }

    .link {
        padding: 0 5px 0 40px;
    }
}
