.root {
    display: flex;
}

.appointment {
    margin-bottom: 112px;
}

.header {
    margin-bottom: 40px;
}

.title {
    margin-bottom: 16px;
}

.subtitle {
    font-size: 22px;
    line-height: 32px;
    font-weight: 400;
}

.content {
    max-width: 560px;
    padding: 24px;
}

.background {
    display: none;
}

.logo {
    display: block;
    width: 160px;
    height: 32px;
    margin: 8px 0 80px;
}

.block {
    margin-bottom: 64px;
}

.button {
    margin-top: 32px;
}

@media (min-width: 1024px) {
    .column {
        min-height: 100vh;
        width: 50%;
    }

    .column {
        &_content {
            display: flex;
            justify-content: flex-end;
            padding-right: 64px;
        }
    }

    .background {
        display: block;
    }

    .content {
        padding: 24px 40px;
    }

    .appointment,
    .social-media {
        margin-bottom: 160px;
    }
}
